import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-new-service-fee',
  templateUrl: './new-service-fee.component.html',
  styleUrls: ['./new-service-fee.component.scss']
})
export class NewServiceFeeComponent implements OnInit {
  addServiceFee: FormGroup;
  monthlyServiceRate: any;
  quaterlyFee: any = 0;
  monthlyRate: any = 0;
  qDiscount: any = 0;
  yDiscount: any = 0;
  qDiscountValue: any = 0;
  yDiscountValue: any = 0;
  quaterlyRateFinal: any;
  categoryDet: any;
  finalDiscountValue: any;
  yearlyFee: any = 0;
  yearlyRateFinal: any;
  finalYDiscountValue: any;
  serviceFeeData: any;
  AddServiceFeeRecords: boolean = false;
  subCategoryServices: any;
  serviceName: any;
  catCode: any;
  subCategoryCode: any;
  serviceSubCatName: any;
  categoriesCheck: boolean = true;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder, ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listCategories();
    this.quaterlyFee = 0;
    this.finalDiscountValue = 0;
    this.quaterlyRateFinal = 0;
    this.yearlyFee = 0;
    this.yearlyRateFinal = 0;
    this.addServiceFee = this.formBuilder.group({
      baseHousehold: ['', Validators.required],
      monthlyFee: [0, Validators.required],
      quaterlyFee: [0, Validators.required],
      qDiscount: [0, Validators.required],
      yearlyFee: [0, Validators.required],
      yDiscount: [0, Validators.required],
      serviceCategoryName: ['', Validators.required],
      serviceSubCategoryName: ['', Validators.required],
      checkCategory: new FormControl(false),
      checkSubCategory: new FormControl(false),

    })
    this.addServiceFee.get("checkSubCategory").disable()
  }

  householdsCount(households) {
    //console.log("households" + households)
  }

  monthlyFeeRates(monthlyRate) {
    //console.log("monthlyRate" + monthlyRate)
    this.monthlyServiceRate = monthlyRate
    if (this.qDiscountValue != 0) {
      this.quaterlyFee = (this.monthlyServiceRate) * (this.qDiscountValue / 100)
      this.finalDiscountValue = (this.monthlyServiceRate) - (this.quaterlyFee)
      this.quaterlyRateFinal = (this.finalDiscountValue) * 3
    }
    if (this.yDiscountValue != 0) {
      this.yearlyFee = (this.monthlyServiceRate) * (this.yDiscountValue / 100)
      this.finalYDiscountValue = (this.monthlyServiceRate) - (this.yearlyFee)
      this.yearlyRateFinal = (this.finalYDiscountValue) * 12
    }
  }

  quaterlyDiscounts(qDiscount) {
    this.qDiscountValue = qDiscount;
    this.quaterlyFee = (this.monthlyServiceRate) * (qDiscount / 100)
    this.finalDiscountValue = (this.monthlyServiceRate) - (this.quaterlyFee)
    this.quaterlyRateFinal = (this.finalDiscountValue) * 3
  }

  yearlyDiscounts(yDiscount) {
    this.yDiscountValue = yDiscount;
    this.yearlyFee = (this.monthlyServiceRate) * (yDiscount / 100)
    this.finalYDiscountValue = (this.monthlyServiceRate) - (this.yearlyFee)
    this.yearlyRateFinal = (this.finalYDiscountValue) * 12
  }

  submit() {
    let obj = {
      "cat_code": this.catCode,
      "serviceCategoryName": this.serviceName,
      "subcat_code": this.subCategoryCode,
      "serviceSubCategoryName": this.serviceSubCatName,
      "baseHousehold": this.addServiceFee.value.baseHousehold,
      "monthlyFee": this.addServiceFee.value.monthlyFee,
      "quaterlyFee": this.quaterlyRateFinal,
      "yearlyFee": this.yearlyRateFinal,
      "qDiscount": this.addServiceFee.value.qDiscount,
      "yDiscount": this.addServiceFee.value.yDiscount
    }
    this.http.post(Global.url + '/addNewServiceFeeRates/', obj)
      .subscribe(data => {
        var serviceData = data.json();
        this.serviceFeeData = serviceData.response;
        if (serviceData.status == 200) {
          this.AddServiceFeeRecords = false;
          this.toastr.success("Added ServiceFees Data Successfully");
          this.router.navigate(['/admin/listNewServiceFee'])
        }
        else {
          this.toastr.warning("Record adding Failed")
          this.AddServiceFeeRecords = true;
        }
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })
  }

  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          //console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  listSelectedSubCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/selectedSubCategories/' + this.catCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoryServices  = response.response;
          //console.log("subCategoryServices" + JSON.stringify( this.subCategoryServices))
        } else {
        //  this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  listSubCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategories', options)
      .subscribe(data => {
        var response = data.json();
        //console.log("response" + JSON.stringify(response))
        if (response.status == 200) {
          this.subCategoryServices = response.response
          //console.log("category" + JSON.stringify(this.subCategoryServices))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }


  changeCategory(x) {
    this.catCode = x
    //console.log("category" + x)
    if (x == "") {
      this.addServiceFee.get("checkCategory").enable()
      return false;
    } else {
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/subcategories/' + x, options)
        .subscribe(data => {
          var response = data.json();
          if (response.status == 200) {
            this.subCategoryServices = response.response
            this.addServiceFee.get("checkSubCategory").enable()

            this.addServiceFee.get("checkCategory").enable()
            this.getCategoryName(x)
            return false;
          } else {
            this.toastr.warning("No Records Found")
          }
        }, error => {
          this.toastr.warning("Unable to connect to server")
        })
    }
  }

  //CATEGORY NAMES
  getCategoryName(catCode) {
    //console.log("catCode" + catCode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categoryName/' + catCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceName = response.response[0].categoryName
          //console.log("response" + JSON.stringify(response.response[0].categoryName))
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  subCategory(subCatCode) {
    this.subCategoryCode = subCatCode
    //console.log("subCategory" + subCatCode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoryName/' + subCatCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceSubCatName = response.response[0].subcategoryName
          //console.log("response" + JSON.stringify(response.response[0].subcategoryName))
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })

  }

  cancel() {
    this.router.navigate(['/admin/listNewServiceFee'])
  }

  // reset(){
  //   this.addServiceFee.reset();
  //   this.addServiceFee.get("checkSubCategory").disable()
  //   this.addServiceFee.get("checkCategory").enable()
  // }


  selectAllCategories() {
    if (this.addServiceFee.value.checkCategory == true) {
      this.addServiceFee.value.checkSubCategory = true
      this.addServiceFee.get("checkSubCategory").enable()
      this.listSubCategories();
      //console.log("check true")
    } else {
      this.addServiceFee.get("checkSubCategory").disable()
      this.subCategoryServices = []
      //console.log("check false")
    }
  }

  selectAllSubCategories(){
    if(this.addServiceFee.value.checkSubCategory == true){
      //console.log("subCategoryServices"+JSON.stringify(this.subCategoryServices))
      this.listSelectedSubCategories();
    }else{
      this.subCategoryServices = []
    }
  }

  baseChange() {
    this.subCategoryServices.forEach((e) => {
      e.baseHousehold = this.addServiceFee.value.baseHousehold
      e.monthlyFee = this.addServiceFee.value.monthlyFee
      //console.log(e)
    });
  }

  monthlyChange() {
    this.subCategoryServices.forEach((e) => {
      e.baseHousehold = this.addServiceFee.value.baseHousehold
      e.monthlyFee = this.addServiceFee.value.monthlyFee
      //console.log(e)
    });
  }


  batchSubmit() {
    //console.log("categoryArray" + JSON.stringify(this.subCategoryServices))
    this.http.post(Global.url + '/addBatchServiceFeeRates/', this.subCategoryServices)
      .subscribe(data => {
        var serviceData = data.json();
        this.serviceFeeData = serviceData.response;
        if (serviceData.status == 200) {
          this.AddServiceFeeRecords = false;
          this.toastr.success("Added ServiceFees Data Successfully");
          this.router.navigate(['/admin/listNewServiceFee'])
        }
        else {
          this.toastr.warning("Record adding Failed")
          this.AddServiceFeeRecords = true;
        }
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })

  }

  
home(){
  this.router.navigate(['/admin/dashboard'])
}




}
