import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Global } from '../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';

@Component({
  providers: [Beyond],
  selector: 'app-edit-service-fees',
  templateUrl: './edit-service-fees.component.html',
  styleUrls: ['./edit-service-fees.component.css']
})
export class EditServiceFeesComponent implements OnInit {
  editServiceFeesHouseholds: FormGroup;
  viewId: any;
  viewSerFeeDet: any;
  categoryDet: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    //this.categoryDetails()
    this.viewId = this.activatedRoute.snapshot.params['id'];
    this.viewServiceFees(this.viewId)
    this.editServiceFeesHouseholds = this.formBuilder.group({
      noOfHouseholds:['', Validators.required],
      monthly: ['', Validators.required],
      quarterly: ['', Validators.required],
      yearly: ['', Validators.required],
      promoCode: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required],
    })
   
  }
  // categoryDetails(){
  //   var headers = new Headers();
  //   headers.append("Accept", 'application/json');
  //   headers.append('Content-Type', 'application/json');
  //   let options = new RequestOptions({ headers: headers });
  //   this.http.get(Global.url + '/categories', options)
  //     .subscribe(data => {
  //       var response = data.json();
  //       if (response.status == 200) {
  //         this.categoryDet = response.response;
  //         //console.log("This is Response" + JSON.stringify(this.categoryDet))
  //         //this.toastr.success("servicefee details list")
  //         //  this.router.navigate(['/addserv'])
  //       }
  //       else {
  //         this.toastr.warning("No Records Found")
  //       }
  //       error => {
  //         //console.log('The execution is failed ' + JSON.stringify(error))
  //       }
  //     })
  // }
  viewServiceFees(viewId) {
    this.beyond.beyondAdmin();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewServiceFeesHouseholds/' + viewId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.viewSerFeeDet = response.response[0];
          //console.log("This is Response" + JSON.stringify(this.viewSerFeeDet))
          this.editServiceFeesHouseholds = this.formBuilder.group({
            noOfHouseholds: [this.viewSerFeeDet.noOfHouseholds, Validators.required],
            monthly: [this.viewSerFeeDet.monthly, Validators.required],
            quarterly: [this.viewSerFeeDet.quarterly, Validators.required],
            yearly: [this.viewSerFeeDet.yearly, Validators.required],
            promoCode: [this.viewSerFeeDet.promoCode, Validators.required],
            startDate: [this.viewSerFeeDet.startDate, Validators.required],
            endDate: [this.viewSerFeeDet.endDate, Validators.required],
          })
          //this.toastr.success("servicefee details list")
        }
        else {
          this.toastr.warning("No Records Found")
        }

        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  submit(){
    let obj = {
      "id":this.viewSerFeeDet.id,
      "noOfHouseholds":this.editServiceFeesHouseholds.value.noOfHouseholds,
      "monthly":this.editServiceFeesHouseholds.value.monthly,
      "quarterly":this.editServiceFeesHouseholds.value.quarterly,
      "yearly":this.editServiceFeesHouseholds.value.yearly,
      "promoCode":this.editServiceFeesHouseholds.value.promoCode,
      "startDate":this.editServiceFeesHouseholds.value.startDate,
      "endDate":this.editServiceFeesHouseholds.value.endDate
    }
    //alert("This is obj: "+JSON.stringify(obj))
    //console.log("This is obj: "+JSON.stringify(obj))
    this.http.post(Global.url+'/editServiceFeesHouseholds/',obj)
    .subscribe(data =>{
      var addData = data.json();
       this.toastr.success("Edit ServiceFeesHouseholds Data Successfully");
       this.router.navigate(['/admin/listServiceFeesHouseholds'])
    },
    error => {
      //console.log('The execution is failed ' + JSON.stringify(error))
    })
  }
  cancelServiceCategory(){
    this.router.navigate(['/admin/listServiceFeesHouseholds'])
  }
  home(){
    this.router.navigate(['/admin/dashboard'])
  }
}
