import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Global } from '../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';

@Component({
  providers: [Beyond],
  selector: 'app-add-service-fees',
  templateUrl: './add-service-fees.component.html',
  styleUrls: ['./add-service-fees.component.css']
})
export class AddServiceFeesComponent implements OnInit {
  addServiceCategory: FormGroup;
  categoryDet: any;
  public categoryName;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) {  
    this.addServiceCategory = this.formBuilder.group({
    categoryName: ['', Validators.required],
    noOfHouseholds: ['', Validators.required],
    monthly: ['', Validators.required],
    quarterly: ['', Validators.required],
    yearly: ['', Validators.required],
  })
}

  ngOnInit() {
    this.beyond.beyondAdmin();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          //console.log("This is Response" + JSON.stringify(this.categoryDet))
          //this.toastr.success("servicefee details list")
          //  this.router.navigate(['/addserv'])
        }
        else {
          this.toastr.warning("No Records Found")
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
   
  }
  submitData(){
    //console.log(this.addServiceCategory)
    let obj = {
      "categoryName":this.addServiceCategory.value.categoryName,
      "noOfHouseholds":this.addServiceCategory.value.noOfHouseholds,
      "monthly":this.addServiceCategory.value.monthly,
      "quarterly":this.addServiceCategory.value.quarterly,
      "yearly":this.addServiceCategory.value.yearly,
      "status":"ACTIVE"
    }
    //console.log("This is obj: "+JSON.stringify(obj))
    this.http.post(Global.url+'/addServiceFees/',obj)
    .subscribe(data =>{
      var addData = data.json();
       this.toastr.success("Added ServiceFeesHouseholds Data Successfully");
       this.router.navigate(['/listServiceFeesHouseholds'])
    },
    error => {
      //console.log('The execution is failed ' + JSON.stringify(error))
    })
  }
  cancelServiceCategory(){
    this.router.navigate(['/listServiceFeesHouseholds'])
  }
  home(){
    this.router.navigate(['/admin/dashboard'])
  }
  list(){
    this.router.navigate(['/listServiceFeesHouseholds'])
  }
}
