import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


@Component({
  providers: [Beyond],
  selector: 'app-add-service-fees1',
  templateUrl: './add-service-fees1.component.html',
  styleUrls: ['./add-service-fees1.component.css']
})
export class AddServiceFees1Component implements OnInit {
  addServiceFees: FormGroup;
  cat_code: any;
  subcat_code: any;
  viewFeeService: any;
  listFeeServicevalue: any;
  categoryName: any;
  subcategoryName: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    private toastr: ToastrService,
    private formBuilder: FormBuilder
  ) { 
  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.cat_code = localStorage.getItem('cat_code');
    this.subcat_code = localStorage.getItem('subcat_code');
    this.gettingCategoryCodeByCategoryName(this.cat_code);
    this.gettingCategoryCodeBySubCategoryName(this.subcat_code);
    this.addServiceFees = this.formBuilder.group({
      cat_code: ['', Validators.required],
      subcat_code: ['', Validators.required],
      minNoOfHouseholds: ['', Validators.required],
      maxNoOfHouseholds: ['', Validators.required],
      feeForMonthly: ['', Validators.required],
      feeForQuarterly: ['', Validators.required],
      feeForYearly: ['', Validators.required],
    })
  }

  gettingCategoryCodeByCategoryName(cat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories/'+cat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.categoryName = response.response[0].categoryName;
        //console.log("This is Response" + JSON.stringify(this.categoryName));
        this.addServiceFees = this.formBuilder.group({
          cat_code: [this.categoryName, Validators.required],
          subcat_code: ['', Validators.required],
          minNoOfHouseholds: ['', Validators.required],
          maxNoOfHouseholds: ['', Validators.required],
          feeForMonthly: ['', Validators.required],
          feeForQuarterly: ['', Validators.required],
          feeForYearly: ['', Validators.required],
        })
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  gettingCategoryCodeBySubCategoryName(subcat_code){
    var headers = new Headers(); 
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoriesView/'+subcat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.subcategoryName = response.response[0].subcategoryName;
        //console.log("This is Response2" + JSON.stringify(this.subcategoryName));
        this.addServiceFees = this.formBuilder.group({
          cat_code: [this.categoryName, Validators.required],
          subcat_code: [this.subcategoryName, Validators.required],
          minNoOfHouseholds: ['', Validators.required],
          maxNoOfHouseholds: ['', Validators.required],
          feeForMonthly: ['', Validators.required],
          feeForQuarterly: ['', Validators.required],
          feeForYearly: ['', Validators.required],
        })
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  addServiceFee(){
    let obj = {
      cat_code:this.cat_code,
      subcat_code:this.subcat_code,
      minNoOfHouseholds:this.addServiceFees.value.minNoOfHouseholds,
      maxNoOfHouseholds:this.addServiceFees.value.maxNoOfHouseholds,
      feeForMonthly:this.addServiceFees.value.feeForMonthly,
      feeForQuarterly:this.addServiceFees.value.feeForQuarterly,
      feeForYearly:this.addServiceFees.value.feeForYearly,
    }
    //alert("this is Obj: "+JSON.stringify(obj))
    this.http.post(Global.url + '/addServiceFees',obj)
      .subscribe(data => {
        var response = data.json();
        if(response.status == "200"){
          //alert("Hii>>: "+JSON.stringify(response));
        this.router.navigate(['/listServiceFeesHouseholds1'])
      }else{
       // alert("Hiis: ")
      }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  cancelServiceFee(){
    this.router.navigate(['/listServiceFeesHouseholds1'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
