import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({  
  providers:[Beyond],
  selector: 'app-add-service-sub-category',
  templateUrl: './add-service-sub-category.component.html',
  styleUrls: ['./add-service-sub-category.component.css']
})
export class AddServiceSubCategoryComponent implements OnInit {
  addServiceSubCategory: FormGroup;
  serviceCategoryCode: any;
  serviceCategoryName: any;
  localCategoryCode: any;
  
  constructor(    
    public beyond:Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    public route: ActivatedRoute,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.serviceCategoryCode = this.route.snapshot.params['categoryCode'];
    this.localCategoryCode = JSON.parse(localStorage.getItem('serviceCategoryData'))
    //console.log("localCategoryCode"+JSON.stringify(this.localCategoryCode));
    //this.serviceCategoryName = this.route.snapshot.params['name'];
    //console.log("category Code"+this.serviceCategoryCode);
    this.addServiceSubCategory = this.formBuilder.group({
      cat_code: [this.serviceCategoryCode, Validators.required],
      subcat_code: ['', Validators.required],
      subcategoryName: ['', Validators.required],
      subcategoryDescription: ['', Validators.required],
    })
  }

  submit() {
    let obj = {
      "cat_code": "" + this.serviceCategoryCode,
      "subcat_code": "" +this.addServiceSubCategory.value.subcat_code,
      "subcategoryName": "" + this.addServiceSubCategory.value.subcategoryName,
      "subcategoryDescription": "" + this.addServiceSubCategory.value.subcategoryDescription,
      "status": "ACTIVE"
    }
    //console.log("this is SubCategory: "+JSON.stringify(obj))
    this.http.post(Global.url +'/addSubCategory', obj)
      .subscribe(data => {
        var serviceSubCategoryData = data.json();
        //console.log("serviceSubCategoryData"+JSON.stringify(serviceSubCategoryData))
          this.toastr.success('Service Sub-Categories Added Successfuly...');
          this.router.navigate(['/listServiceSubCategories', this.serviceCategoryCode ])
      }, error => {
        //console.log(error);// Error getting the data
      });
  }
  cancelServiceSubCategory(){
    this.router.navigate(['/listServiceSubCategories', this.serviceCategoryCode])
  }

}
