import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global'
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers:[Beyond],
  selector: 'app-edit-service-categories',
  templateUrl: './edit-service-categories.component.html',
  styleUrls: ['./edit-service-categories.component.css']
})
export class EditServiceCategoriesComponent implements OnInit {
  categoryCode: any;
  editServiceCategory: FormGroup;
  editService: any;
  viewServiceCategoryData: any;
  viewServiceData: any;
  showViewService: boolean = false;
  constructor(
    public beyond:Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.categoryCode = this.route.snapshot.params['categoryCode'];
    console.log("category Code"+this.categoryCode);
    //this.editService(this.categoryCode)
    this.editServiceCategory = this.formBuilder.group({
      cat_code: ['', Validators.required],
      categoryName: ['', Validators.required],
      categoryDescription: ['', Validators.required],
      status: ['', Validators.required],
    })
    this.viewServiceCategory(this.categoryCode)
  }

  viewServiceCategory(categoryCode){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url+'/categories/'+ categoryCode, options)
      .subscribe(data => {
        this.viewServiceCategoryData = data.json();
        this.viewServiceData = this.viewServiceCategoryData.response[0];
        this.editServiceCategory = this.formBuilder.group({
          cat_code: [this.viewServiceData.cat_code, Validators.required],
          categoryName: [this.viewServiceData.categoryName, Validators.required],
          categoryDescription: [this.viewServiceData.categoryDescription, Validators.required],
          status: [this.viewServiceData.status, Validators.required],
        })
        this.showViewService = true;
        console.log("service category Data"+JSON.stringify(this.viewServiceData))
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  submit() {
    let obj = {
      "cat_code":this.viewServiceData.cat_code,
      "categoryName":this.editServiceCategory.value.categoryName,
      "categoryDescription":this.editServiceCategory.value.categoryDescription,
      "status":this.editServiceCategory.value.status,

    }
    console.log("obj"+JSON.stringify(obj))
    this.http.post(Global.url+'/editCategoryService', obj)
      .subscribe(data => {
        var serviceCategoryData = data.json();
        console.log("serviceCategoryData"+JSON.stringify(serviceCategoryData))
        this.toastr.success('Service Categories Updated Successfuly...');
        this.router.navigate(['/admin/listServiceCategories'])
      }, error => {
        this.toastr.warning('Unable to Connect to Server')// Error getting the data
      });
  }
  cancelServiceCategory(){
    this.router.navigate(['/admin/listServiceCategories'])
  }

  home(){
    this.router.navigate(['/admin/dashboard'])
  }

}
