import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl } from '@angular/forms';


@Component({
  providers: [Beyond],
  selector: 'app-per-lead-service-fee',
  templateUrl: './per-lead-service-fee.component.html',
  styleUrls: ['./per-lead-service-fee.component.scss']
})
export class PerLeadServiceFeeComponent implements OnInit {
  addServiceFee: FormGroup; 
  categoryDet: any;
  finalDiscountValue: any; 
  serviceFeeData: any;
  AddServiceFeeRecords: boolean = false;
  subCategoryServices: any;
  serviceName: any;
  catCode: any;
  subCategoryCode: any;
  serviceSubCatName: any;
  submitted: boolean;
  perleadDataservice: any;
  catDataservice: any;
  catData:any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listCategories();
    this.addServiceFee = this.formBuilder.group({
      perLead: ['', Validators.required],     
      serviceCategoryName: ['', Validators.required],
      serviceSubCategoryName: ['', Validators.required],
      checkCategory: new FormControl(false),
      checkSubCategory: new FormControl(false),
    })
    //  this.addServiceFee.get("checkSubCategory").disable()
  }

  get f() {
    return this.addServiceFee.controls;
  }

  onSubmit() {
  
    this.submitted = true;
    if (this.addServiceFee.value.perLead == "") {
      return;
    }else if (this.addServiceFee.value.serviceCategoryName == "") {
      return;
    }else if (this.addServiceFee.value.serviceSubCategoryName == "" ) {
      return;
    }

    console.log(this.addServiceFee.value.serviceCategoryName )
    if(this.addServiceFee.value.checkCategory == true ){
      this.perleadCost();
    }else if(this.addServiceFee.value.checkSubCategory == true ){
      this.perleadCost()
    }
    else if(this.addServiceFee.value.serviceCategoryName != undefined && this.addServiceFee.value.serviceSubCategoryName != undefined){
      this.perleadsubCode()
    }else if(this.addServiceFee.value.serviceCategoryName != undefined){
      this.perleadcatCode()
    }

    }
 
    
  
  update(){
    let obj = {
      "cat_code": this.catCode,
      "serviceCategoryName": this.serviceName,
      "subcat_code": this.subCategoryCode,
      "serviceSubCategoryName": this.serviceSubCatName,
      "perLead": this.addServiceFee.value.perLead,
    }
    this.http.post(Global.url + '/addPerLeadServiceFeeRates/', obj)
      .subscribe(data => {
        var serviceData = data.json();
        this.serviceFeeData = serviceData.response;
        if (serviceData.status == 200) {
          this.AddServiceFeeRecords = false;
          this.toastr.success("Added ServiceFees Data Successfully");
          this.router.navigate(['/admin/listPerLeadServiceFee'])
        }
        else {
          this.toastr.warning("Record adding Failed")
          this.AddServiceFeeRecords = true;
        }
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })
  }

  /* -- Update Perlead service -- */
  perleadCost(){
  let costObj = {
    "perLead": this.addServiceFee.value.perLead,
  }
  console.log("costobj",costObj)
  this.http.post(Global.url + '/updatePerLeadServiceFeeRates/', costObj)
    .subscribe(data => {
      var perleadData = data.json();
      this.perleadDataservice = perleadData.response;
      if (perleadData.status == 200) {
        this.toastr.success("Updated ServiceFees Data Successfully");
        this.router.navigate(['/admin/listPerLeadServiceFee'])
      }
      else {
        this.toastr.warning("Record updated Failed")
        this.AddServiceFeeRecords = true;
      }
    },
      error => {
        
      })

}
 /* -- Update Perlead service/ -- */


 /* -- Update Perlead cat_Code service -- */
 perleadcatCode(){
  let costObj = {
    "perLead": this.addServiceFee.value.perLead,
    "cat_code": this.addServiceFee.value.serviceCategoryName
  }
  console.log("costobj",costObj)
  this.http.post(Global.url + '/updatecatCodePerLeadServiceFeeRates', costObj)
    .subscribe(data => {
      var catData = data.json();
      this.catDataservice = catData.response;
      if (catData.status == 200) {
        this.toastr.success("Updated ServiceFees Data Successfully");
        this.router.navigate(['/admin/listPerLeadServiceFee'])
      }
      else {
        this.toastr.warning("Record updated Failed")
        this.AddServiceFeeRecords = true;
      }
    },
      error => {
        
      })

}
 /* -- Update Perlead cat_Code service/ -- */

/* -- Update Perlead sucat_Code service -- */
perleadsubCode(){
  let costObj = {
    "perLead": this.addServiceFee.value.perLead,
    "cat_code": this.addServiceFee.value.serviceCategoryName,
    "subcat_code":this.addServiceFee.value.serviceSubCategoryName

  }
  console.log("costobj",costObj)
  this.http.post(Global.url + '/updatecatsubcatCodePerLeadServiceFeeRates', costObj)
    .subscribe(data => {
      var perleadcatsubData = data.json();
      console.log("codes>>"+JSON.stringify(perleadcatsubData))
      this.perleadDataservice = perleadcatsubData.response;
   
      if (perleadcatsubData.status == 200) {
        this.toastr.success("Updated ServiceFees Data Successfully");
        this.router.navigate(['/admin/listPerLeadServiceFee'])
      }
      else {
        this.toastr.warning("Record updated Failed")
        this.AddServiceFeeRecords = true;
      }
    },
      error => {
        
      })

}
 /* -- Update Perlead sucat_Code service/ -- */

  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          //console.log("category"+JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }


  changeCategory(x) {
    this.catCode = x
    //console.log("category" + x)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/perLeadSubcategories/' + x, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoryServices = response.response
          this.getCategoryName(x)
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  //CATEGORY NAMES
  getCategoryName(catCode) {
    //console.log("catCode" + catCode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categoryName/' + catCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceName = response.response[0].categoryName
          //console.log("response" + JSON.stringify(response.response[0].categoryName))
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  subCategory(subCatCode) {
    this.subCategoryCode = subCatCode
    //console.log("subCategory" + subCatCode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoryName/' + subCatCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceSubCatName = response.response[0].subcategoryName
          //console.log("response" + JSON.stringify(response.response[0].subcategoryName))
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })

  }

  // cancel() {
  //   this.router.navigate(['/admin/listPerLeadServiceFee'])
  // }

  selectAllCategories() {

    if (this.addServiceFee.value.checkCategory == true) {
      
      this.addServiceFee.get("checkSubCategory").setValue(false);
      this.addServiceFee.get("checkSubCategory").disable()
      this.addServiceFee.get("serviceSubCategoryName").disable();
      this.addServiceFee.get("serviceCategoryName").setErrors(null);
      this.listSubCategories();
      //console.log("check true")
    } else {
      // this.addServiceFee.value.checkSubCategory = false;
      this.addServiceFee.get("checkSubCategory").enable()
      this.addServiceFee.get("serviceSubCategoryName").enable()
      this.addServiceFee.get("serviceCategoryName").setErrors({ required: true });
      this.subCategoryServices = []
      //console.log("check false")
    }
  }

  selectAllSubCategories(){
  
    if(this.addServiceFee.value.checkSubCategory == true){
      //console.log("subCategoryServices"+JSON.stringify(this.subCategoryServices))
      this.addServiceFee.get("checkCategory").enable()
      this.addServiceFee.get("serviceCategoryName").enable()
      this.addServiceFee.get("serviceSubCategoryName").setErrors(null);
      this.listSelectedSubCategories();
    }else{
      this.addServiceFee.get("checkCategory").enable()
      this.addServiceFee.get("serviceCategoryName").enable()
      this.addServiceFee.get("serviceSubCategoryName").setErrors({ required: true});
      this.subCategoryServices = []
    }
  }

  listSelectedSubCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/selectedSubCategories/' + this.catCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoryServices  = response.response;
          //console.log("subCategoryServices" + JSON.stringify( this.subCategoryServices))
        } else {
        //  this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  listSubCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategories', options)
      .subscribe(data => {
        var response = data.json();
        //console.log("response" + JSON.stringify(response))
        if (response.status == 200) {
          this.subCategoryServices = response.response
          console.log("category" + JSON.stringify(this.subCategoryServices))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  perLeadChange() {
    this.subCategoryServices.forEach((e) => {
      e.perLead = this.addServiceFee.value.perLead
      //console.log(e)
    });
  }

  batchSubmit() {
    console.log("categoryArray" + JSON.stringify(this.subCategoryServices))
    this.http.post(Global.url + '/addBatchPerLeadServiceFee/', this.subCategoryServices)
      .subscribe(data => {
        var serviceData = data.json();
        this.serviceFeeData = serviceData.response;
        if (serviceData.status == 200) {
          this.AddServiceFeeRecords = false;
          this.toastr.success("Added ServiceFees Data Successfully");
          this.router.navigate(['/admin/listPerLeadServiceFee'])
        }
        else {
          this.toastr.warning("Record adding Failed")
          this.AddServiceFeeRecords = true;
        }
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })

  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
