import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'
@Component({
  providers: [Beyond],
  selector: 'app-list-of-proz',
  templateUrl: './list-of-proz.component.html',
  styleUrls: ['./list-of-proz.component.css']
})
export class ListOfProzComponent implements OnInit {
  listProzData: any;
  dataListProz: any;

  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listOfProzs();
  }

  listOfProzs() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfProzDetails', options)
      .subscribe(data => {
        this.listProzData = data.json();
        this.dataListProz = this.listProzData.response;
        //console.log("service category Data" + JSON.stringify(this.listProzData))
        //console.log("service category Data dataListProz" + JSON.stringify(this.dataListProz))

        for (var i = 0; i < this.dataListProz.length; i++) {
          if (this.dataListProz[i].backGroundCheckStatus == "APPROVE") {
            this.dataListProz[i].approveStatus = true;
            this.dataListProz[i].rejectStatus = true;
            this.dataListProz[i].disqualifyStatus = false;
          } else if (this.dataListProz[i].backGroundCheckStatus == "REJECT") {
            this.dataListProz[i].approveStatus = false;
            this.dataListProz[i].rejectStatus = true;
            this.dataListProz[i].disqualifyStatus = false;
          } else if (this.dataListProz[i].backGroundCheckStatus == "DISQUALIFY") {
            this.dataListProz[i].approveStatus = true;
            this.dataListProz[i].rejectStatus = true;
            this.dataListProz[i].disqualifyStatus = true;
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })

  }

  toggleProfileStatus(x,y) {
    event.preventDefault();
    event.stopPropagation();
    if (y == "APPROVE") {
      x.approveStatus = true;
      x.rejectStatus = false;
      x.disqualifyStatus = false;
      this.statusToggler(x, 'APPROVE')
    } else if (y == "REJECT") {
      x.approveStatus = false;
      x.rejectStatus = true;
      x.disqualifyStatus = false;
      this.statusToggler(x, 'REJECT')
    } else if (y == "DISQUALIFY") {
      x.approveStatus = false;
      x.rejectStatus = false;
      x.disqualifyStatus = true;
      this.statusToggler(x, 'DISQUALIFY')
    } else {
      x.approveStatus = false;
      x.rejectStatus = false;
      x.disqualifyStatus = false;
      this.statusToggler(x, 'PENDING')
    }
  }
  statusToggler(x, y) {
    //console.log("y..." + y)
    var obj = {
      businessPhone: x.businessPhone,
      backGroundCheckStatus: y,
    }

    this.http.post(Global.url + '/updateProfileStatus', obj)
      .subscribe(data => {
        var status = data.json();
        //console.log('This is status '+JSON.stringify(status))
        if (status.status == 200) {
          this.listOfProzs()
        } else {
          ////alert('Unable to update Profile status')
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  view(data){
    //console.log("id"+JSON.stringify(data))
    this.router.navigate(['/admin/viewProzProfileDetails',data.prozId])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
