import { Component, OnInit, ViewChild } from '@angular/core';
import {Beyond} from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'
import { stringify } from 'querystring';
import { Export2Excel } from '../../../utilities/export2Excel'
declare var jsPDF: any;
declare var XLSX: any;

@Component({
  providers:[Beyond, Export2Excel],
  selector: 'app-question-master',
  templateUrl: './question-master.component.html',
  styleUrls: ['./question-master.component.scss']
})
export class QuestionMasterComponent implements OnInit {
  @ViewChild('myInput') myInput
  serviceCategoryData: any;
  tableServiceCategoryData: any;
  dataServiceCategory: boolean = false;
  deleteServiceCategoryData: any;
  categoriesData: any;
  SearchCategory: any;

  display = 'none';
  val: any;
  displayFileModal = 'none';
  filesToUpload: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  percentage: any;
  quesprogressShow: boolean = false;
  quesprogressHide: boolean = false;
  quespercentage: any;

  constructor(
    public beyond:Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    public export2Excel: Export2Excel,
  ) { }


  ngOnInit() {
    this.beyond.beyondAdmin();
  }

  onCloseHandled() {
    this.displayFileModal = 'none';
  }

  closeImport() {
    this.progressShow = false;
    this.progressHide = false;
    event.preventDefault();
    this.filesToUpload = 0;

  }

  closeImportques(){

  }
  
  openImport() {
    this.progressShow = false;
    this.progressHide = false;
    event.preventDefault();
    event.stopPropagation();
    this.displayFileModal = 'block';
    //this.val = x;
  }

  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.progressShow = false;
      this.progressHide = false;
      this.filesToUpload = event.target.files[0];
    } else {
      this.progressShow = false;
      this.progressHide = false;
    }
  }
  

  import($event) {
    const url = Global.url + '/optionsImport'
    console.log("url"+url)
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('file', this.filesToUpload)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage == 100) {
          this.toastr.success('Data imported Successfully...')
          this.progressHide = true;
          //this.img = false;
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {

    };
    xhr.send(formData);
  }



  selectques(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.quesprogressShow = false;
      this.quesprogressHide = false;
      this.filesToUpload = event.target.files[0];
      console.log(this.filesToUpload)
    } else {
      this.quesprogressShow = false;
      this.quesprogressHide = false;
    }
  }
  

  importques($event) {
    const url = Global.url + '/questionsImport'
    console.log("url"+url)
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('file', this.filesToUpload)
    console.log(formData)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.quespercentage = (e.loaded / e.total) * 100;
        this.quesprogressShow = true;
        this.quesprogressHide = false;
        if (this.quespercentage == 100) {
          this.toastr.success('Data imported Successfully...')
          this.quesprogressHide = true;
          //this.img = false;
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {

    };
    xhr.send(formData);
  }


home(){
  this.router.navigate(['/admin/dashboard'])
}

}
