import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global'
import {googlemaps} from 'googlemaps';
import { GeocodingService } from '../../../utilities/geocode.service';
import { HttpClient, JsonpInterceptor } from "@angular/common/http";
import * as L from "leaflet";
import { icon, latLng, Map, marker, point, polyline, tileLayer } from 'leaflet';
declare var $: any;
declare var google: any;

@Component({
  providers: [Beyond, GeocodingService],
  selector: 'app-view-proz-profile-details',
  templateUrl: './view-proz-profile-details.component.html',
  styleUrls: ['./view-proz-profile-details.component.css']
})
export class ViewProzProfileDetailsComponent implements OnInit {
  json: any;
  map: L.Map;
  prozId: any;
  prozDetailsData: any;
  prozProfileData: any;
  prozServiceData: any;
  prozPaymentData: any;
  dateTimeData: any;
  statusValue: boolean = false;
  ratingsResponse: boolean = false;
  estimatedServiceCost: any = 0;
  totalHouseHolds: any = 0;
  infoWindowSet: any;
  prozLocationFetch: any;
  mapSet: any;
  layerSet: any;
  ratingsDetails: any;
  geocoder: google.maps.Geocoder;
  public latitude: number = 51.673858;
  public longitude: number = 7.815982;
  public zipcodes = [];
  countyStore:any=[]
  countyStatus: boolean = false;
  showMap: boolean = false;
  imagePreview: string = "assets/img/proz/company_default.jpg";
  streetMaps = tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    detectRetina: true,
    // attribution: '&amp;copy; &lt;a href="https://www.openstreetmap.org/copyright"&gt;OpenStreetMap&lt;/a&gt; contributors'
  });

  wMaps = tileLayer('http://maps.wikimedia.org/osm-intl/{z}/{x}/{y}.png', {
    detectRetina: true,
    // attribution: '&amp;copy; &lt;a href="https://www.openstreetmap.org/copyright"&gt;OpenStreetMap&lt;/a&gt; contributors'
  });
  options = {
    layers: [this.streetMaps],
    zoom: 7,
    center: latLng([46.879966, -121.726909])
  };
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public HttP: HttpClient,
    public activatedRoute: ActivatedRoute,
    public toastr: ToastrService,
    public geocoding: GeocodingService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.prozId = this.activatedRoute.snapshot.params['id']
    this.prozLocation(this.prozId);
    this.viewProzDetails();
    this.getMasterDateTime();
    this.listRatingDetails(this.prozId)
    this.getCountiesList(this.prozId)
  }
    //FETCH PROZ LOCATION
    prozLocation(prozId) {
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/getProzLocation/' + prozId, options)
        .subscribe(data => {
          var response = data.json();
          if (response.status == 200) {
            this.prozLocationFetch = response.response;
            this.getCountiesList(this.activatedRoute.snapshot.params['prozId'])
        //    google.maps.event.addDomListener(window, 'load', this.initializeZipMap(this.prozLocationFetch));
            return false;
          } else {
            this.toastr.warning("No Records Found")
          }
        }, error => {
          this.toastr.warning("Unable to connect to server")
        })
    }
    //FETCH PROZ LOCATION 
  //VIEW PROZ DETAILS
  viewProzDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProDetails/' + this.prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.estimatedServiceCost = 0;
          this.totalHouseHolds = 0;
          this.prozProfileData = response.profile[0];
          this.prozServiceData = response.serviceAreas;
          if (this.prozProfileData.image == 'assets/img/proz/company_default.jpg') {
            this.imagePreview = this.prozProfileData.image
          } else {
            this.imagePreview = Global.url + this.prozProfileData.image
            Global.imagePath = this.prozProfileData.image
          }
          console.log(this.prozServiceData)
          this.prozPaymentData = response.payment[0];
          for (var i = 0; i < response.serviceAreas.length; i++) {
            this.estimatedServiceCost = parseInt(this.estimatedServiceCost) + parseInt(response.serviceAreas[i].perLead)
            this.totalHouseHolds = parseInt(this.totalHouseHolds) + parseInt(response.serviceAreas[i].households)
            this.zipcodes.push(response.serviceAreas[i].zipcode)
          }
          this.statusValue = true;
          // google.maps.event.addDomListener(window, 'load', this.initializeZipMap(response.profile[0]));
          // this.toastr.success("View Proz Details")
        } else {
          this.statusValue = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //VIEW PROZ DETAILS
  //GOOGLE MAP INITIALISATION
  initializeZipMap(profile) {
    // this.geocoder = new google.maps.Geocoder();
    // this.infoWindowSet = new google.maps.InfoWindow();
    // var mapDiv = document.getElementById('google_map');
    // this.mapSet = new google.maps.Map(mapDiv, {
    //   center: new google.maps.LatLng(profile.latitude, profile.longitude),
    //   zoom: 16,
    //   mapTypeId: google.maps.MapTypeId['ROADMAP']
    // });
    // var marker = new google.maps.Marker({
    //   position: new google.maps.LatLng(profile.latitude, profile.longitude),
    //   map: this.mapSet,
    // });
    // this.mapSet.controls[google.maps.ControlPosition['RIGHT_BOTTOM']].push(document.getElementById('googft-legend-open'));
    // this.mapSet.controls[google.maps.ControlPosition['RIGHT_BOTTOM']].push(document.getElementById('googft-legend'));

    // this.layerSet = new google.maps.FusionTablesLayer({
    //   map: this.mapSet,
    //   heatmap: { enabled: false },
    //   query: {
    //     select: "col11",
    //     from: "1qPz-UwllvkOmVkoyszjxmZ8MfEFqdATdBap0GEfg",
    //     where: ""
    //   },
    //   options: {
    //     styleId: 2,
    //     templateId: 2
    //   }
    // });
    // this.layerSet.set("styles", [{
    //   where: "'ZIP' IN('" + this.zipcodes.join("','") + "')",
    //   polygonOptions: {
    //     fillColor: "#00FF00"
    //   }
    // }]);
    // var selected = {};
  }
  //GOOGLE MAP INITIALISATION  
  //GET MASTER DATE TIME
  getMasterDateTime() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/masterDateTime', options)
      .subscribe(data => {
        var response = data.json();
        this.dateTimeData = response.response;
        for (var i = 0; i < this.dateTimeData.length; i++) {
          this.dateTimeData[i].color = true;
          this.dateTimeData[i].colorRed = false;
          if (this.dateTimeData[i].day == "Sunday") {
            this.dateTimeData[i].color = false;
            this.dateTimeData[i].colorRed = true;
            this.dateTimeData[i].fromTime = "Closed";
            this.dateTimeData[i].toTime = "";
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //GET MASTER DATE TIME
  //OK BUTTON
  okButton() {
    this.router.navigate(['/admin/listOfProz'])
  }
  //OK BUTTON

  // LIST RATINGS
  listRatingDetails(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listRatings/'+ prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.ratingsDetails = response.response;
          this.ratingsResponse = true;
          //console.log("This is Response" + JSON.stringify(this.ratingsDetails))
        }
        else {
          this.ratingsResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  // LIST RATINGS
    //STATES
    onMapReady(map: Map, key) {
      this.HttP.get("assets/geolocations.json").subscribe((json: any) => {
        json.features.map(val => {
          val.checked = false
        })
        this.json = json;
        var myStyle = { // Define your style object
          "color": "blue",
          "fillColor": ' rgb(242,239,233)',
          "weight": 0.2,
          "fillOpacity": 0.05,
          "opacity": 1,
         
      };
        // this.countyStore.forEach((a)=>{
          
        L.geoJSON(this.json, {
          style:myStyle,
   
          onEachFeature: (feature, layer) => {
  
            // popup content
            var popupContent = '<h4>NAME: ' + feature.properties.NAME + '</h4><b>COUNTY: ' + feature.properties.COUNTY + '</b>';
            // pop content ends here
            //Open data in Popup on Map
            layer.bindPopup(popupContent);
            // Open data in Popoup on map ends
            // Click event on Map
            let name = feature.properties.NAME.toUpperCase()
            this.countyStore.forEach((a) => {
              if (a.county == name&&a.stateCode==feature.properties.STATE) {
                feature['checked'] = true
                var style = {
                  fillColor: 'yellow',
                  fillOpacity: 0.5,
                  weight: 0.2,
                  opacity: 1,
                }
                var highlight = {
                  'fillColor': 'green',
                  'weight':0.2,
                  'opacity': 1
                };
                layer['setStyle'](style);
                layer['setStyle'](highlight);
              }
            })
            layer.on({
              'click': (e) => {
                let name = feature.properties.NAME.toUpperCase()
                if (feature['checked'] == true) {
                  e.target.closePopup();
                  feature['checked'] = false
                  var style = {
                    fillColor: ' rgb(242,239,233)',
                    fillOpacity: 0.05,
                    weight: 0.2,
                    opacity: 1,
                  }
                  layer.bindPopup['true'];
                  // this.featuresSelected = feature.properties.NAME;
                  // var val = this.featuresSelected.toUpperCase();
  
                  layer['setStyle'](style);
                  layer['setStyle'](highlight);
                } else if (feature['checked'] == false) {
                  feature['checked'] = true
                  var style = {
                    fillColor: 'green',
                    fillOpacity: 0.5,
                    weight: 0.2,
                    opacity: 1,
                }
                  var highlight = {
                    'fillColor': 'green',
                    'weight':0.2,
                    'opacity': 1
                  };
                  // this.featuresSelected = feature.properties.NAME;
                  layer['setStyle'](style);
                  layer['setStyle'](highlight);
                  // this.getCountyManualy(this.featureSelected)
                }
              }
            });
            // Click event on Map ends here
          },
        }).addTo(map);
        L.marker([this.prozLocationFetch[0].latitude,this.prozLocationFetch[0].longitude]).addTo(map);
        map.setView([this.prozLocationFetch[0].latitude,this.prozLocationFetch[0].longitude], 7);
      });
    }
      //GET COUNTIES LIST
      getCountiesList(proCode) {
        var headers = new Headers();
        headers.append("Accept", 'application/json');
        headers.append('Content-Type', 'application/json');
        let options = new RequestOptions({ headers: headers });
        this.http.get(Global.url + '/getUniqueCounty/' + proCode, options)
          .subscribe(data => {
            var response = data.json();
            var categoryName = []
            if (response.status == 200) {
              this.countyStatus = true
              this.countyStore = response.response;
              console.log(this.countyStore)
              this.showMap = true    
              //LOAD LAYER
    
              // this.loadSelected(this.countyStore)
              //LOAD LAYER
    
            } else {
              
              // this.spinner.hide()
            }
          }, error => {
            this.toastr.warning("Unable to connect to server")
          })
      }
      //GET COUNTIES LIST
      gotoTerms(){
        this.router.navigate(['/proz/terms',this.activatedRoute.snapshot.params['prozId']])
      }
      home(){
        this.router.navigate(['/admin/dashboard'])
      }
}
