import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-service-fees1',
  templateUrl: './edit-service-fees1.component.html',
  styleUrls: ['./edit-service-fees1.component.css']
})
export class EditServiceFees1Component implements OnInit {
  editServiceFee: FormGroup
  cat_code: any;
  subcat_code: any;
  viewFeeService: any;
  listFeeServicevalue: any;
  categoryName: any;
  subcategoryName: any;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    private toastrService: ToastrService,
    private formBuilder: FormBuilder,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.viewFeeService = JSON.parse(localStorage.getItem('data'))
    //console.log("this is ViewData :"+JSON.stringify(this.viewFeeService));
    this.gettingCategoryCodeByCategoryName(this.viewFeeService.cat_code);
    this.gettingCategoryCodeBySubCategoryName(this.viewFeeService.subcat_code);
    this.editServiceFee = this.formBuilder.group({
      cat_code: [this.categoryName, Validators.required],
      subcat_code: ['', Validators.required],
      minNoOfHouseholds: ['', Validators.required],
      maxNoOfHouseholds: ['', Validators.required],
      feeForMonthly: ['', Validators.required],
      feeForQuarterly: ['', Validators.required],
      feeForYearly: ['', Validators.required],
    })

  }

  gettingCategoryCodeByCategoryName(cat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories/'+cat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.categoryName = response.response[0].categoryName;
        this.editServiceFee = this.formBuilder.group({
          cat_code:this.categoryName,
          subcat_code:this.subcat_code,
          minNoOfHouseholds:this.viewFeeService.minNoOfHouseholds,
          maxNoOfHouseholds:this.viewFeeService.maxNoOfHouseholds,
          feeForMonthly:this.viewFeeService.feeForMonthly,
          feeForQuarterly:this.viewFeeService.feeForQuarterly,
          feeForYearly:this.viewFeeService.feeForYearly,
        })
        //console.log("This is Response" + JSON.stringify(this.categoryName));
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  gettingCategoryCodeBySubCategoryName(subcat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoriesView/'+subcat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.subcategoryName = response.response[0].subcategoryName;
        this.editServiceFee = this.formBuilder.group({
          cat_code:this.categoryName,
          subcat_code:this.subcategoryName,
          minNoOfHouseholds:this.viewFeeService.minNoOfHouseholds,
          maxNoOfHouseholds:this.viewFeeService.maxNoOfHouseholds,
          feeForMonthly:this.viewFeeService.feeForMonthly,
          feeForQuarterly:this.viewFeeService.feeForQuarterly,
          feeForYearly:this.viewFeeService.feeForYearly
        })
        //console.log("This is Response2" + JSON.stringify(this.subcategoryName));
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
    }
  updateServiceFees(){
    let obj = {
      cat_code:this.viewFeeService.cat_code,
      subcat_code:this.viewFeeService.subcat_code,
      minNoOfHouseholds:this.editServiceFee.value.minNoOfHouseholds,
      maxNoOfHouseholds:this.editServiceFee.value.maxNoOfHouseholds,
      feeForMonthly:this.editServiceFee.value.feeForMonthly,
      feeForQuarterly:this.editServiceFee.value.feeForQuarterly,
      feeForYearly:this.editServiceFee.value.feeForYearly,
    }
    this.http.post(Global.url + '/editServiceFeesHouseholds',obj)
      .subscribe(data => {
        var response = data.json();
        if(response.status == "200"){
        this.router.navigate(['/listServiceFeesHouseholds1'])
      }else{
      }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  cancelServiceFees(){
    this.router.navigate(['/listServiceFeesHouseholds1'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
