import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';


@Component({
  providers:[Beyond],
  selector: 'app-add-service-categories',
  templateUrl: './add-service-categories.component.html',
  styleUrls: ['./add-service-categories.component.css']
})
export class AddServiceCategoriesComponent implements OnInit {

  addServiceCategory: FormGroup; 

  constructor(
    public beyond:Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.addServiceCategory = this.formBuilder.group({
      cat_code: ['', Validators.required],
      categoryName: ['', Validators.required],
      categoryDescription: ['', Validators.required],
    })
  }

  submit() {
    let obj = {
      "cat_code":this.addServiceCategory.value.cat_code,
      "categoryName":this.addServiceCategory.value.categoryName,
      "categoryDescription":this.addServiceCategory.value.categoryDescription,
       "status": "ACTIVE"
    }
    this.http.post(Global.url + '/addCategory', obj)
      .subscribe(data => {
        var serviceCategoryData = data.json();
        console.log("serviceCategoryData"+JSON.stringify(serviceCategoryData))
          this.toastr.success('Service Categories Added Successfuly...');
          this.router.navigate(['/admin/listServiceCategories'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }
  cancelServiceCategory(){
    this.router.navigate(['/admin/listServiceCategories'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
 list(){
  this.router.navigate(['/admin/listServiceCategories'])
 }
}
