import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import {Calendar} from '../../../../utilities/calendar';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  providers:[Beyond, Calendar],
  selector: 'app-admin-dispatchboard',
  templateUrl: './admin-dispatchboard.component.html',
  styleUrls: ['./admin-dispatchboard.component.scss']
})
export class AdminDispatchboardComponent implements OnInit {

  constructor(
    public beyond:Beyond,
    public calendar:Calendar,
    public router: Router,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.calendar.calendar()
  }

  searchServiceCategory(){
    this.router.navigate(['/searchServiceCategory'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
