import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-view-service-fees1',
  templateUrl: './view-service-fees1.component.html',
  styleUrls: ['./view-service-fees1.component.css']
})
export class ViewServiceFees1Component implements OnInit {
  cat_code: any;
  subcat_code: any;
  viewFeeService: any;
  listFeeServicevalue: any;
  catName: any;
  subcatName: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    private toastr: ToastrService
  ) { 
  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.viewFeeService = JSON.parse(localStorage.getItem('data'))
    //console.log("this is ViewData :"+JSON.stringify(this.viewFeeService));
    this.gettingCategoryCodeByCategoryName(this.viewFeeService.cat_code);
    this.gettingCategoryCodeBySubCategoryName(this.viewFeeService.subcat_code)
  }

  gettingCategoryCodeByCategoryName(cat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories/'+cat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.catName = response.response[0].categoryName;
        //console.log("This is Response" + JSON.stringify(this.catName));
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  gettingCategoryCodeBySubCategoryName(subcat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoriesView/'+subcat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.subcatName = response.response[0].subcategoryName;
        //console.log("This is Response2" + JSON.stringify(this.subcatName));
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  editServiceFee(){
    this.router.navigate(['/admin/editServiceFeesHouseholds1',this.viewFeeService.cat_code, this.viewFeeService.subcat_code])
  }

  okServiceFee(){
    this.router.navigate(['/admin/listServiceFeesHouseholds1'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
