import { Component, OnInit, ViewChild } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global'
import { stringify } from 'querystring';
import { Export2Excel } from '../../../../utilities/export2Excel'
declare var jsPDF: any;
declare var XLSX: any;
@Component({
  providers:[Beyond, Export2Excel],
  selector: 'app-list-service-categories',
  templateUrl: './list-service-categories.component.html',
  styleUrls: ['./list-service-categories.component.css']
})
export class ListServiceCategoriesComponent implements OnInit {
  @ViewChild('myInput') myInput
  serviceCategoryData: any;
  tableServiceCategoryData: any;
  dataServiceCategory: boolean = false;
  deleteServiceCategoryData: any;
  categoriesData: any;
  SearchCategory: any;

  display = 'none';
  val: any;
  displayFileModal = 'none';
  filesToUpload: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  percentage: any;

  constructor(
    public beyond:Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    public export2Excel: Export2Excel,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listServiceCategories();
  }

   //------------Excel Import&Export---------------
   onCloseHandled() {
    this.displayFileModal = 'none';
  }

  closeImport() {
    this.displayFileModal = 'none';
  }
  openImport() {
    this.progressShow = false;
    this.progressHide = false;
    event.preventDefault();
    event.stopPropagation();
    this.displayFileModal = 'block';
    //this.val = x;
  }

  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.progressShow = false;
      this.progressHide = false;
      this.filesToUpload = event.target.files[0];
    } else {
      this.progressShow = false;
      this.progressHide = false;
    }
  }
  

  import($event) {
    const url = Global.url + '/questionsImport'
    console.log("url"+url)
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('file', this.filesToUpload)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage == 100) {
          this.toastr.success('Data imported Successfully...')
          this.progressHide = true;
          //this.img = false;
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {

    };
    xhr.send(formData);
  }

  listServiceCategories(){
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url +'/categories', options)
        .subscribe(data => {
          this.serviceCategoryData = data.json();
          this.categoriesData = this.serviceCategoryData.response;
          this.dataServiceCategory = true;
          console.log("service category Data"+JSON.stringify(this.categoriesData))
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })
    
  }

  comingsoon(){
    alert("Coming Soon")
  }

  addServiceCategory(){
    this.router.navigate(['/admin/addServiceCategory'])
  }
  
  editServiceCategory(categoryCode){
    console.log("catcode"+JSON.stringify(categoryCode))
    this.router.navigate(['/admin/editServiceCategory', categoryCode.cat_code])
  }

  alert(){
   // alert("Not Yet Implemented...!")
  }
  
  listServiceSubCategories(categoryCode){
    localStorage.setItem('serviceCategoryData', JSON.stringify(categoryCode))
    console.log("data.."+categoryCode.categoryCode)
    this.router.navigate(['/admin/listServiceSubCategories', categoryCode.cat_code])
  }

  viewServiceCategory(categoryCode){
    this.router.navigate(['/admin/viewServiceCategory', categoryCode.cat_code])
}

  deleteServiceCategory(categoryCode){
    this.http.get(Global.url + '/deleteServiceCategory/'+categoryCode.cat_code)
      .subscribe(data => {
        this.deleteServiceCategoryData = data.json();
        console.log("deleteServiceCategoryData"+JSON.stringify(this.deleteServiceCategoryData))
          this.listServiceCategories()
          this.toastr.success('Category Deleted Successfully....!')
      }, error => {
        this.toastr.warning('Unable to Connect to Server Error While Toggeling')
      })
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}

}
