import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { EasyPieChart } from '../../../../utilities/easypiechart';
import { EasyPieChartInit } from '../../../../utilities/easypiechart-init'
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  providers:[Beyond,EasyPieChart, EasyPieChartInit],
  selector: 'app-list-estimates',
  templateUrl: './list-estimates.component.html',
  styleUrls: ['./list-estimates.component.css']
})
export class ListEstimatesComponent implements OnInit {

  constructor(
    public beyond:Beyond,
    public easyPieChart:EasyPieChart,
    public easyPieChartInit:EasyPieChartInit,
    public router: Router,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.easyPieChart.easyPieChart();
    this.easyPieChartInit.easyPieChartInit();
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
