import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Beyond} from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Global } from '../../../../global/global';

@Component({
  providers:[Beyond],
  selector: 'app-list-service-fees',
  templateUrl: './list-service-fees.component.html',
  styleUrls: ['./list-service-fees.component.css']
})
export class ListServiceFeesComponent implements OnInit {
 
  serviceFeeDetails: FormGroup;
  constructor(
    public beyond:Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) {
    this.serviceFeeDetails = this.formBuilder.group({
    categoryName: ['', Validators.required],
    noOfHouseholds: ['', Validators.required],
    monthly: ['', Validators.required],
    quarterly: ['', Validators.required],
    yearly: ['', Validators.required],
    status: ['', Validators.required],
    createdBy: ['', Validators.required],
    createdOn: ['', Validators.required]
  })
 }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listServiceFeesHouseholds();
  }

 

listServiceFeesHouseholds(){
  //console.log("hi..")
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  this.http.get(Global.url +'/listServiceFeesHouseholds', options)
    .subscribe(data => {
      var response = data.json();
      //alert("hi " + response)
      this.serviceFeesDetailsSave = response.response;
      //console.log("This is Response" + JSON.stringify(this.serviceFeesDetailsSave))
      if(response.status==200){
        this.toastr.success("servicefee details list")
      //  this.router.navigate(['/addserv'])
       }
else{
  this.toastr.warning("No Records Found")
}
      
      error => {
      //console.log('The execution is failed ' + JSON.stringify(error))
    }
    })

}

addServiceFeeDetails(){
  this.router.navigate(['/addServiceFees'])
}
serviceFeesDetailsSave(){
  let obj = {
    "categoryName":this.serviceFeeDetails.value.categoryName,
    "noOfHouseholds":this.serviceFeeDetails.value.noOfHouseholds,
    "monthly": this.serviceFeeDetails.value.monthly,
    "quarterly": this.serviceFeeDetails.value.quarterly,
    "yearly": this.serviceFeeDetails.value.yearly,
    "status": this.serviceFeeDetails.value.status,
    "createdBy": this.serviceFeeDetails.value.createdBy,
    "createdOn": this.serviceFeeDetails.value.createdOn,
    
  }
  //console.log("obj"+JSON.stringify(obj))
  this.http.post(Global.url+'/addServiceFeesHouseholds', obj)
  .subscribe(data => {
    var response = data.json();
    //console.log("This is Response: " + JSON.stringify(response))
    if(response.status === 200){
      this.toastr.success("Service Fees Saved Successfully...!")
    this.router.navigate(['/listServiceFeesHouseholds'])
  }else {
    this.toastr.warning('Service Fees Creation Failed...');
  } error => {
    //console.log('The execution is failed ' + JSON.stringify(error))
  }
  })
  
}
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
