import { stringify } from 'querystring';
import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global'
@Component({
  providers: [Beyond],
  selector: 'app-list-service-sub-categories',
  templateUrl: './list-service-sub-categories.component.html',
  styleUrls: ['./list-service-sub-categories.component.css']
})
export class ListServiceSubCategoriesComponent implements OnInit {
  categoryCode: any;
  serviceSubCategoryData: any;
  dataServiceSubCategory: boolean = false;
  deleteServiceSubCategoryData: any;
  serviceCategoryCode: any;
  serviceCategoryName: any;
  localCategoryCode: any;
  subCategoriesData: any;
  noFound: any;
  serviceFeeDetails: any;
  serviceFeeResponse: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.localCategoryCode = JSON.parse(localStorage.getItem('serviceCategoryData'))
    this.serviceCategoryCode = this.route.snapshot.params['categoryCode'];
    this.serviceSubCategory(this.serviceCategoryCode)
  }

  addServiceSubCategory(serviceCategoryCode) {
    this.router.navigate(['/admin/addServiceSubCategory', serviceCategoryCode])
  }

  editServiceSubCategory(subCategoryCode) {
    this.router.navigate(['/admin/editServiceSubCategory', subCategoryCode.subcat_code])
  }

  viewServiceSubCategory(subCategoryCode) {
    this.router.navigate(['/admin/viewServiceSubCategory', subCategoryCode.subcat_code])
  }
  serviceSubCategory(serviceCategoryCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subcategories/' + serviceCategoryCode, options)
      .subscribe(data => {
        this.serviceSubCategoryData = data.json();
        if (this.serviceSubCategoryData.status == "200") {
          this.subCategoriesData = this.serviceSubCategoryData.response;
          this.dataServiceSubCategory = true;
        } else {
          this.noFound = "No Records Found"
          this.dataServiceSubCategory = false;
        }

      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  deleteServiceSubCategory(subCategoryCode) {
    this.http.get(Global.url+'/deleteServiceSubCategory/'+subCategoryCode.subcat_code)
      .subscribe(data => {
        this.deleteServiceSubCategoryData = data.json();
        this.serviceSubCategory(subCategoryCode.cat_code)
        this.toastr.success('Sub Category Deleted Successfully....!')
      }, error => {
        this.toastr.warning('Unable to Connect to Server Error While Toggeling')
      })
  }

  serviceFeeCategory(data){
    localStorage.setItem('data', JSON.stringify(data));
    this.router.navigate(['/admin/listServiceFeesHouseholds1'])
  }

  listQuestions(data){
    localStorage.setItem('data', JSON.stringify(data));
    this.router.navigate(['/admin/listQuestions'])
  }

  back(){
    this.router.navigate(['/admin/listServiceCategories'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
