import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-per-lead-service-fee',
  templateUrl: './edit-per-lead-service-fee.component.html',
  styleUrls: ['./edit-per-lead-service-fee.component.scss']
})
export class EditPerLeadServiceFeeComponent implements OnInit {
  editServiceFee: FormGroup;
  monthlyServiceRate: any;
  quaterlyFee: any = 0;
  quaterlyRateFinal: any;
  categoryDet: any;
  finalDiscountValue: any;
  yearlyFee: any = 0;
  yearlyRateFinal: any;
  finalYDiscountValue: any;
  serviceFeeData: any;
  editServiceFeeRecords: boolean = false;
  subCategoryServices: any;
  serviceName: any;
  viewNewServiceFee: any;
  viewNewServiceFee1: any;
  id: any;
  catCode: any;
  subCategoryCode: any;
  serviceSubCatName: any;
  showViewService: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder, 
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['id']

    this.viewServiceCategory();
    this.listCategories();
    this.beyond.beyondAdmin();
    this.quaterlyFee = 0;
    this.finalDiscountValue = 0;
    this.quaterlyRateFinal = 0;
    this.yearlyFee = 0;
    this.finalYDiscountValue = 0;
    this.yearlyRateFinal = 0;
    this.editServiceFee = this.formBuilder.group({
      serviceCategoryName: ['', Validators.required],
      perLead: ['', Validators.required],
      serviceSubCategoryName: ['', Validators.required]
    })
  }


  update() {
    let obj = {
      "id": this.id,
      "catCode": this.catCode,
      "subCategoryCode": this.subCategoryCode,
      "serviceCategoryName": this.serviceName,
      "serviceSubCategoryName": this.serviceSubCatName,
      "perLead": this.editServiceFee.value.perLead
    }
    //console.log("obj" + JSON.stringify(obj))
    this.http.post(Global.url + '/editPerLeadServiceFeeRates/', obj)
      .subscribe(data => {
        var serviceData = data.json();
        this.serviceFeeData = serviceData.response;
        if (serviceData.status == 200) {
          this.editServiceFeeRecords = false;
          this.toastr.success("ServiceFees Data Updated Successfully");
          this.router.navigate(['/admin/listPerLeadServiceFee'])
        }
        else {
          this.toastr.warning("Record adding Failed")
          this.editServiceFeeRecords = true;
        }
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })
  }

  viewServiceCategory() {
    //console.log("Tis is Viwew: ")
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewOfPerLeadServiceFee/' + this.id, options)
      .subscribe(data => {
        this.viewNewServiceFee = data.json();
        this.viewNewServiceFee1 = this.viewNewServiceFee.response[0];
        //console.log("data of service fee"+JSON.stringify(this.viewNewServiceFee1))
        this.editServiceFee.get("serviceCategoryName").setValue(this.viewNewServiceFee1.cat_code)
        this.editServiceFee.get("serviceSubCategoryName").setValue(this.viewNewServiceFee1.subcat_code)
        this.editServiceFee.get("perLead").setValue(this.viewNewServiceFee1.perLead)
        this.changeCategory(this.viewNewServiceFee1.cat_code)
        this.subCategory(this.viewNewServiceFee1.subcat_code)
        this.showViewService = true;

      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }


  changeCategory(x) {
    //console.log("category" + x)
    this.catCode = x
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subcategories/' + x, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoryServices = response.response
          this.getCategoryName(x)
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  //CATEGORY NAMES
  getCategoryName(catCode) {
    //console.log("catCode" + catCode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categoryName/' + catCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceName = response.response[0].categoryName
          //console.log("response" + JSON.stringify(response.response[0].categoryName))
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  subCategory(subCatCode) {
    this.subCategoryCode = subCatCode
    //console.log("subCategory" + subCatCode)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoryName/' + subCatCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceSubCatName = response.response[0].subcategoryName
          //console.log("response" + JSON.stringify(response.response[0].subcategoryName))
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })

  }

  cancel() {
    this.router.navigate(['/admin/listNewServiceFee'])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
