import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../utilities/beyond';
import {Calendar} from '../../../utilities/calendar';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  providers:[Beyond, Calendar],
  selector: 'app-admin-dashboard',
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.css']
})
export class AdminDashboardComponent implements OnInit {
  loginData: any;
  name: any;
  role: any;
  constructor(
    public beyond:Beyond,
    public calendar:Calendar,
    public router: Router,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    //console.log("loginData"+this.loginData)
    this.name = this.loginData.name;
    //console.log("loginData"+this.name)
    this.role = this.loginData.role;
  }

  // searchServiceCategory(){
  //   this.router.navigate(['/searchServiceCategory'])
  // }
  home(){
    this.router.navigate(['/admin/dashboard'])
  }
}
