import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-list-per-lead-service-fee',
  templateUrl: './list-per-lead-service-fee.component.html',
  styleUrls: ['./list-per-lead-service-fee.component.scss']
})
export class ListPerLeadServiceFeeComponent implements OnInit {
  serviceFeeDetails: any;
  serviceFeeResponse: boolean = false;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listServiceFeeDetails();
  }

  listServiceFeeDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfPerLeadServiceFee', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceFeeDetails = response.response;
          this.serviceFeeResponse = true;
          console.log("This is Response" + JSON.stringify(this.serviceFeeDetails))
        }
        else {
          this.serviceFeeResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  editPerLeadService(id) {
    //console.log("this is id: " + id);
    this.router.navigate(['/admin/editPerLeadServiceFee', id])
  }

  addPerLeadServiceFee(){
    this.router.navigate(['/admin/perLeadServiceFee'])
  }
  home(){
    this.router.navigate(['/admin/dashboard'])
  }
}
