import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';

@Component({
  providers: [Beyond],
  selector: 'app-view-service-fees',
  templateUrl: './view-service-fees.component.html',
  styleUrls: ['./view-service-fees.component.css']
})
export class ViewServiceFeesComponent implements OnInit {
  viewId: any;
  viewSerFeeDet: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.viewId = this.activatedRoute.snapshot.params['id'];
    alert("hi " + this.viewId)
    this.viewServiceFees(this.viewId)

  }
  viewServiceFees(viewId) {
    this.beyond.beyondAdmin();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewServiceFeesHouseholds/' + viewId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.viewSerFeeDet = response.response[0];
          //console.log("This is Response" + JSON.stringify(this.viewSerFeeDet))
          this.toastr.success("servicefee details list")
          //  this.router.navigate(['/addserv'])
        }
        else {
          this.toastr.warning("No Records Found")
        }

        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  submit() {
    this.router.navigate(['/admin/listServiceFeesHouseholds'])
  }

  EditService(viewSerFeeDet) {
    //alert("This is Edit: "+JSON.stringify(viewSerFeeDet.id))
    this.router.navigate(['/admin/editServiceFeesHouseholds', viewSerFeeDet.id])
  }
  home(){
    this.router.navigate(['/admin/dashboard'])
  }
}
