import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global'
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers:[Beyond],
  selector: 'app-edit-service-sub-category',
  templateUrl: './edit-service-sub-category.component.html',
  styleUrls: ['./edit-service-sub-category.component.css']
})
export class EditServiceSubCategoryComponent implements OnInit {
  subCategoryCode: any;
  editServiceSubCategory: FormGroup;
  viewServiceSubCategoryData: any;
  viewServiceSubData: any;
  showViewServiceSub: boolean = false;
  constructor( 
    public beyond:Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.subCategoryCode = this.route.snapshot.params['subCategoryCode'];
    //console.log("Sub Category Code"+this.subCategoryCode);
    this.viewServiceSubCategory(this.subCategoryCode)
    this.editServiceSubCategory = this.formBuilder.group({
      cat_code: ['', Validators.required],
      subcat_code: ['', Validators.required],
      subcategoryName: ['', Validators.required],
      subcategoryDescription: ['', Validators.required],
      status: ['', Validators.required],
    })
   
  }

  viewServiceSubCategory(subCategoryCode){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url+'/subCategoriesView/'+ subCategoryCode, options)
      .subscribe(data => {
        this.viewServiceSubCategoryData = data.json();
        this.viewServiceSubData = this.viewServiceSubCategoryData.response[0];
        this.editServiceSubCategory = this.formBuilder.group({
          cat_code: [this.viewServiceSubData.cat_code, Validators.required],
          subcat_code: [this.viewServiceSubData.subcat_code, Validators.required],
          subcategoryName: [this.viewServiceSubData.subcategoryName, Validators.required],
          subcategoryDescription: [this.viewServiceSubData.subcategoryDescription, Validators.required],
          status: [this.viewServiceSubData.status, Validators.required],
        })
        this.showViewServiceSub = true;
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }


  submit() {
    let obj = {
      "cat_code": "" + this.viewServiceSubData.cat_code,
      "subcat_code": "" + this.viewServiceSubData.subcat_code,
      "subcategoryName": "" + this.editServiceSubCategory.value.subcategoryName,
      "subcategoryDescription": "" + this.editServiceSubCategory.value.subcategoryDescription,
      "status": "" + this.editServiceSubCategory.value.status,
    }
    this.http.post(Global.url+'/editSubCategory', obj)
      .subscribe(data => {
        var serviceSubCategoryData = data.json();
        //console.log("serviceCategoryData"+JSON.stringify(serviceSubCategoryData))
        this.toastr.success('Service Sub-Categories Updated Successfuly...');
        this.router.navigate(['/admin/listServiceSubCategories', this.viewServiceSubData.cat_code])
      }, error => {
        this.toastr.warning('Unable to Connect to Server')// Error getting the datacat_code
      });
  }
  cancelServiceSubCategory(){
    this.router.navigate(['/admin/listServiceSubCategories', this.viewServiceSubData.cat_code])
  }

  home(){
    this.router.navigate(['/admin/dashboard'])
  }
}
