import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-list-service-fees-households1',
  templateUrl: './list-service-fees-households1.component.html',
  styleUrls: ['./list-service-fees-households1.component.css']
})
export class ListServiceFeesHouseholds1Component implements OnInit {
  feeList: any;
  cat_code: any;
  subcat_code: any;
  catName: any;
  subcategoryName: any;
  listFeeService: any;
  serviceFeeDetails: any;
  listFeeServicevalue: boolean = false;
  serviceFeeResponse: boolean = false;
  noRecords: any;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute, 
    private http: Http,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.feeList = JSON.parse(localStorage.getItem('data'))
    this.listServiceFeeDetails();
    //console.log("This is List: "+JSON.stringify(this.feeList));
    this.cat_code = this.feeList.cat_code;
    this.subcat_code = this.feeList.subcat_code;
    this.subcategoryName = this.feeList.subcategoryName;
    this.gettingCategoryCodeByCategoryName(this.cat_code);
    //this.listServiceFee(this.cat_code,this.subcat_code);
  }

  listServiceFeeDetails(){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listServiceFeesHouseholds', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceFeeDetails = response.response;
          this.serviceFeeResponse = true;
          //console.log("This is Response" + JSON.stringify(this.serviceFeeDetails))
        }
        else {
          this.serviceFeeResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
   
  }

  gettingCategoryCodeByCategoryName(cat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories/'+cat_code, options)
      .subscribe(data => {
        var response = data.json();
        this.catName = response.response[0].categoryName;
        //console.log("This is Response" + JSON.stringify(this.catName));
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  listServiceFee(cat_code,subcat_code){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfServiceFeesHouseholdsforfee1/'+cat_code+"/"+subcat_code, options)
      .subscribe(data => {
        var response = data.json();
        if(response.status == "200"){
          this.listFeeService = response.response;
          //console.log("This is listResponse: " + JSON.stringify(this.listFeeService))
          this.listFeeServicevalue = true;
        }else{
           this.noRecords = response.response;
           //console.log("This is NoRecords: "+this.noRecords)
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  addServiceFeeHousehold1(){
    localStorage.setItem('cat_code',this.cat_code)
    localStorage.setItem('subcat_code',this.subcat_code)
    this.router.navigate(['/admin/addServiceFeesHouseholds1',this.cat_code,this.subcat_code])    
  }

  editServiceFeesHouseholds1(){
    this.router.navigate(['/admin/editServiceFeesHouseholds1'])    
  }

  viewServiceFeesHouseholds1(data){
    localStorage.setItem('data', JSON.stringify(data))
    this.router.navigate(['/admin/viewServiceFeesHouseholds1',data.cat_code,data.subcat_code])    
  }

  back(){
    this.router.navigate(['/admin/listServiceSubCategories',this.cat_code])
  }

}
