import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { editableTable } from '../../../../utilities/editableTable';
@Component({
  providers: [Beyond, editableTable],
  selector: 'app-list-service-fees-household',
  templateUrl: './list-service-fees-household.component.html',
  styleUrls: ['./list-service-fees-household.component.css']
})
export class ListServiceFeesHouseholdComponent implements OnInit {
  addServiceCategory: FormGroup;
  addServiceFeeList: FormGroup;
  addMasterServiceFeeDiscount: FormGroup;
  serviceFeesDetailsSave: any;
  serviceFeeDetails: any;
  categoryDet: any;
  categoryName = "APP";
  dataNoRecords: any;
  dataDeleteRecords: any;
  serviceFeesSubDetails: any;
  masterServiceFeeDetails: any;
  categoryNoRecords: boolean = false;
  AddServiceFeeRecords: boolean = false;
  AddServiceFeeDiscountsRecords: boolean = false;
  masterServiceValuesResponse: boolean = false;
  masterServiceFeeResponse: boolean = false;
  serviceFeeResponse: boolean = false;
  AddRecords: boolean = false;
  categoryDeleteRecords: boolean = false;
  items: FormArray;
  fromHouseHoldsCount: any;
  toHouseHoldsCount: any;
  houseHoldsCount: any;
  discount: any;
  monthly: any;
  quaterly: any;
  yearly: any;
  monthlyFix: any;
  percentage: any;
  quaterlyDiscountRate: any;
  quaterlyDiscount: any;
  yearlyDiscountRate: any;
  yearlyDiscount: any;
  fixedHouseHolds: any;
  masterserviceValuesDetails: any;
  constructor(
    public beyond: Beyond,
    public editTable: editableTable,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.editTable.tableEdit();
    // this.listCategories()
    this.listServiceFeeDetails()
    this.serviceFeeDiscounts()
    this.masterServiceValuesList()
    this.categoryChange(this.categoryName);
    this.addServiceCategory = this.formBuilder.group({
      cat_code: [this.categoryName],
      subcategoryName: ['', Validators.required],
      minNoOfHouseholds: ['', Validators.required],
      maxNoOfHouseholds: ['', Validators.required],
      feeForMonthly: ['', Validators.required],
      feeForQuarterly: ['', Validators.required],
      feeForYearly: ['', Validators.required]
    })

    this.addServiceFeeList = this.formBuilder.group({
      service: this.formBuilder.array([])
    })

    this.addMasterServiceFeeDiscount = this.formBuilder.group({
      masterServiceFee: this.formBuilder.array([])
    })

  }

  createServiceFee(): FormGroup {
    return this.formBuilder.group({
      noOfHouseholds: '',
      monthly: '',
      quarterly: '',
      yearly: '',
      promoCode: '',
      startDate: '',
      endDate: '',
    });
  }

  createMasterServiceFeeDiscounts(): FormGroup {
    return this.formBuilder.group({
      fromHouseHolds: '',
      toHouseHolds: '',
      discount: ''
    });
  }

  addFieldValue() {
    this.items = this.addServiceFeeList.get('service') as FormArray;
    this.items.push(this.createServiceFee());
  }


  get serviceFeeValues(): FormArray {
    return this.addServiceFeeList.get('service') as FormArray;
  }

  deleteFieldValue(index: number) {
    this.serviceFeeValues.removeAt(index);
  }


  // Master Service Fee Discounts
  addMasterFieldValue() {
    this.items = this.addMasterServiceFeeDiscount.get('masterServiceFee') as FormArray;
    this.items.push(this.createMasterServiceFeeDiscounts());
  }

  get masterServiceFeeValues(): FormArray {
    return this.addMasterServiceFeeDiscount.get('masterServiceFee') as FormArray;
  }

  deleteMasterServiceFieldValue(index: number) {
    this.masterServiceFeeValues.removeAt(index);
  }

  houseHolds() {
    for (var i = 0; i < this.addServiceFeeList.get('service').value.length; i++) {
      for (var j = 0; j < this.masterServiceFeeDetails.length; j++) {
        this.fromHouseHoldsCount = parseInt(this.masterServiceFeeDetails[j].fromHouseHolds)
        this.toHouseHoldsCount = parseInt(this.masterServiceFeeDetails[j].toHouseHolds)
        this.houseHoldsCount = parseInt(this.addServiceFeeList.get('service').value[i].noOfHouseholds)
        
        this.quaterlyDiscountRate = 5 / 100;
        this.yearlyDiscountRate = 10 / 100;
        this.percentage = 100/100
        this.quaterlyDiscount = 3;
        this.yearlyDiscount = 12;
        this.fixedHouseHolds = 10000;

        if (this.houseHoldsCount == 10000) {
          this.monthly = 50;
          this.quaterly = this.monthly * this.quaterlyDiscount * (this.percentage - this.quaterlyDiscountRate)          
          this.yearly = this.monthly * (this.percentage - this.yearlyDiscountRate) * this.yearlyDiscount
        } else if (this.houseHoldsCount >= this.fromHouseHoldsCount && this.houseHoldsCount <= this.toHouseHoldsCount) {
          this.discount = this.masterServiceFeeDetails[j].discount;
          this.monthlyFix = 50;
          this.monthly = this.monthlyFix * (this.percentage - this.discount / 100) * this.houseHoldsCount / this.fixedHouseHolds
          this.quaterlyCalculations(this.monthly)
        }
      }
    }
  }

  quaterlyCalculations(monthly) {
    this.quaterlyDiscountRate = 5 / 100;
    this.yearlyDiscountRate = 10 / 100;
    this.percentage = 100/100;
    this.quaterlyDiscount = 3;
    this.yearlyDiscount = 12;
    this.quaterly = monthly * this.quaterlyDiscount * (this.percentage-this.quaterlyDiscountRate);
    this.yearly = monthly * (this.percentage-this.yearlyDiscountRate) * this.yearlyDiscount;
  }

  submit() {
    for (var i = 0; i < this.addServiceFeeList.get('service').value.length; i++) {
      //console.log("addServiceFeeList" + this.addServiceFeeList.get('service').value.length)

      let obj = {
        "noOfHouseholds": '' + this.addServiceFeeList.get('service').value[i].noOfHouseholds,
        "monthly": this.monthly,
        "quarterly": this.quaterly,
        "yearly": this.yearly,
        "promoCode": '' + this.addServiceFeeList.get('service').value[i].promoCode,
        "startDate": '' + this.addServiceFeeList.get('service').value[i].startDate,
        "endDate": '' + this.addServiceFeeList.get('service').value[i].endDate,
      }

     // alert("This is obj: " + JSON.stringify(obj))
      this.http.post(Global.url + '/addServiceFees/', obj)
        .subscribe(data => {
          var addServiceFee = data.json();
          this.toastr.success("Added ServiceFeesHouseholds Data Successfully");
          this.serviceFeeValues.reset()
          this.listServiceFeeDetails();
          this.AddServiceFeeRecords = false;
        },
          error => {
            //console.log('The execution is failed ' + JSON.stringify(error))
          })
    }
  }


  listServiceFeeDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listServiceFeesHouseholds', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.serviceFeeDetails = response.response;
          this.serviceFeeResponse = true;
          //console.log("This is Response" + JSON.stringify(this.serviceFeeDetails))
        }
        else {
          this.serviceFeeResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }


  masterServiceValuesList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/masterServiceValues', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.masterserviceValuesDetails = response.response;
          this.masterServiceValuesResponse = true;
          //console.log("Master Service Values" + JSON.stringify(this.masterserviceValuesDetails))
        }
        else {
          this.serviceFeeResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  serviceFeeDiscounts() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/masterServiceFeeDetails', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.masterServiceFeeDetails = response.response;
          this.masterServiceFeeResponse = true;
        }
        else {
          this.masterServiceFeeResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  submitServiceFeeDiscount() {
    for (var i = 0; i < this.addMasterServiceFeeDiscount.get('masterServiceFee').value.length; i++) {
      let discountObj = {
        "fromHouseHolds": '' + this.addMasterServiceFeeDiscount.get('masterServiceFee').value[i].fromHouseHolds,
        "toHouseHolds": '' + this.addMasterServiceFeeDiscount.get('masterServiceFee').value[i].toHouseHolds,
        "discount": '' + this.addMasterServiceFeeDiscount.get('masterServiceFee').value[i].discount,
      }
      this.http.post(Global.url + '/addServiceFeesDiscounts/', discountObj)
        .subscribe(data => {
          var addMasterServiceFee = data.json();
          this.toastr.success("Added ServiceFeesHouseHold Discounts Data Successfully");
          this.masterServiceFeeValues.reset()
          this.serviceFeeDiscounts();
          this.AddServiceFeeDiscountsRecords = false;
        },
          error => {
            //console.log('The execution is failed ' + JSON.stringify(error))
          })
    }
  }


  view(id) {
    //console.log("this is id: " + id);
    this.router.navigate(['/admin/viewServiceFeesHouseholds', id])
  }
  edit(id) {
    //console.log("this is id: " + id);
    this.router.navigate(['/admin/editServiceFeesHouseholds', id])
  }
  addServiceFee() {
    this.AddRecords = true;
  }

  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/categories', options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;

        }
        else {
          this.toastr.warning("No Records Found")
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })

  }
  categoryChange(categoryName) {
    //alert(categoryName)
    this.listOfSubCategories(categoryName)
    this.addServiceCategory = this.formBuilder.group({
      cat_code: [categoryName],
      subcategoryName: ['', Validators.required],
      minNoOfHouseholds: ['', Validators.required],
      maxNoOfHouseholds: ['', Validators.required],
      feeForMonthly: ['', Validators.required],
      feeForQuarterly: ['', Validators.required],
      feeForYearly: ['', Validators.required]
    })
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfServiceFeesHouseholds/' + categoryName, options)
      .subscribe(data => {
        var response = data.json();
        this.serviceFeesDetailsSave = response.response;
        //console.log("This is Response" + JSON.stringify(this.serviceFeesDetailsSave))
        if (response.status == 200) {
          this.toastr.success("servicefee details list")
          //  this.router.navigate(['/addserv'])
          this.categoryNoRecords = false;
        }
        else {
          this.dataNoRecords = response.response;
          //   this.toastr.warning("No Records Found")
          this.categoryNoRecords = true;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  listOfSubCategories(categoryName) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/subCategoriesDetails/' + categoryName, options)
      .subscribe(data => {
        var response = data.json();
        this.serviceFeesSubDetails = response.response;
        //console.log("This is Response" + JSON.stringify(this.serviceFeesSubDetails))
      })
  }

  delete(id) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/deleteServiceFeesHouseholds/' + id, options)
      .subscribe(data => {
        var response = data.json();
        this.serviceFeesDetailsSave = response.response;
        //console.log("This is Response" + JSON.stringify(this.serviceFeesDetailsSave))
        if (response.status == 200) {
          this.toastr.success("servicefee details list")
          this.listServiceFeeDetails();
          //  this.router.navigate(['/addserv'])
          this.categoryDeleteRecords = false;
        }
        else {
          this.dataDeleteRecords = response.response;
          this.toastr.warning("No Records Found")
          this.categoryDeleteRecords = true;
        }

        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  submitData() {
    let obj = {
      "cat_code": this.addServiceCategory.value.cat_code,
      "subcat_code": this.addServiceCategory.value.subcategoryName,
      "minNoOfHouseholds": this.addServiceCategory.value.minNoOfHouseholds,
      "maxNoOfHouseholds": this.addServiceCategory.value.maxNoOfHouseholds,
      "feeForMonthly": this.addServiceCategory.value.feeForMonthly,
      "feeForQuarterly": this.addServiceCategory.value.feeForQuarterly,
      "feeForYearly": this.addServiceCategory.value.feeForYearly,
      "status": "ACTIVE"
    }
    //alert("This is obj: " + JSON.stringify(obj))
    this.http.post(Global.url + '/addServiceFees/', obj)
      .subscribe(data => {
        var addData = data.json();
        this.toastr.success("Added ServiceFeesHouseholds Data Successfully");
        this.categoryChange(this.addServiceCategory.value.cat_code)
        this.AddRecords = false;
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })

  }


  cancelServiceCategory() {
    this.AddRecords = false;
  }


}
