import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global'

@Component({
  providers:[Beyond],
  selector: 'app-view-service-sub-category',
  templateUrl: './view-service-sub-category.component.html',
  styleUrls: ['./view-service-sub-category.component.css']
})
export class ViewServiceSubCategoryComponent implements OnInit {
  subCategoryCode: any;
  viewServiceSubCategoryData: any;
  viewServiceSubData: any;
  showViewServiceSub: boolean = false;
  constructor(
    public beyond:Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.subCategoryCode = this.route.snapshot.params['subCategoryCode'];
    this.viewServiceSubCategory(this.subCategoryCode)
  }

  viewServiceSubCategory(subCategoryCode){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url+'/subCategoriesView/'+ subCategoryCode, options)
      .subscribe(data => {
        this.viewServiceSubCategoryData = data.json();
        this.viewServiceSubData = this.viewServiceSubCategoryData.response[0];
        this.showViewServiceSub = true;
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  editServiceSubCategory(subCategoryCode){
    this.router.navigate(['/editServiceSubCategory', subCategoryCode.subcat_code])
  }

  okServiceSubCategory(){
    this.router.navigate(['/listServiceSubCategories', this.viewServiceSubData.cat_code])
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
list(){
  this.router.navigate(['/listServiceSubCategories', this.viewServiceSubData.cat_code])
}
}
